.app-container {
  background: linear-gradient(135deg, #ff00ff, #00ffff, #ffff00);
  min-height: 100vh;
}

.nav {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.8);
}

.nav-link {
  color: #ff00ff;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  text-shadow: 0 0 10px #ff00ff;
}

.nav-link:hover {
  color: #ffff00;
  text-shadow: 0 0 15px #ffff00;
}

.bonanza-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Arial Black', sans-serif;
  color: #fff;
  position: relative;
}

.wins-ticker {
  background: rgba(0, 0, 0, 0.9);
  padding: 10px;
  text-align: center;
  font-size: 18px;
  color: #ffff00;
  text-shadow: 0 0 10px #ffff00;
  margin-bottom: 20px;
  border: 2px dashed #ff00ff;
}

.win-item {
  margin: 0 20px;
  display: inline-block;
}

.rgb-text {
  animation: rgbShift 1s infinite;
}

@keyframes rgbShift {
  0% { color: #ff0000; text-shadow: 0 0 10px #ff0000; }
  33% { color: #00ff00; text-shadow: 0 0 10px #00ff00; }
  66% { color: #0000ff; text-shadow: 0 0 10px #0000ff; }
  100% { color: #ff0000; text-shadow: 0 0 10px #ff0000; }
}

.title {
  text-align: center;
  font-size: 48px;
  animation: neonPulse 1.5s infinite;
}

@keyframes neonPulse {
  0% { text-shadow: 0 0 20px #ff00ff, 0 0 40px #00ffff; }
  50% { text-shadow: 0 0 30px #ff00ff, 0 0 60px #00ffff; }
  100% { text-shadow: 0 0 20px #ff00ff, 0 0 40px #00ffff; }
}

.user-panel {
  text-align: center;
  margin: 20px 0;
  padding: 15px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  font-size: 24px;
  text-transform: uppercase;
  border: 3px solid #00ffff;
  box-shadow: 0 0 20px #00ffff;
}

.cabinet-link {
  margin-left: 15px;
  text-decoration: none;
  color: #ffff00;
  text-shadow: 0 0 10px #ffff00;
}

.cabinet-link:hover {
  color: #ff00ff;
  text-shadow: 0 0 15px #ff00ff;
}

.slots-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 40px 0;
}

.slot-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slot {
  width: 100px;
  height: 100px;
  background: linear-gradient(45deg, #ff00ff, #00ffff);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px;
  font-weight: bold;
  color: #000;
  border: 5px solid #ffff00;
  box-shadow: 0 0 25px #ffff00;
  position: relative;
  overflow: hidden;
}

.slot.spinning {
  animation: sillySpin 0.5s infinite;
}

@keyframes sillySpin {
  0% { transform: scale(1) rotate(0deg) translateY(0); }
  25% { transform: scale(1.5) rotate(90deg) translateY(-20px); }
  50% { transform: scale(0.5) rotate(180deg) translateY(20px); }
  75% { transform: scale(1.2) rotate(270deg) translateY(-10px); }
  100% { transform: scale(1) rotate(360deg) translateY(0); }
}

.slot-label {
  margin-top: 10px;
  font-size: 16px;
  color: #ff00ff;
  text-shadow: 0 0 10px #ff00ff;
}

.sum-display {
  text-align: center;
  margin: 30px 0;
  font-size: 28px;
  text-transform: uppercase;
}

.red { color: #ff3366; text-shadow: 0 0 15px #ff3366; }
.yellow { color: #ffff66; text-shadow: 0 0 15px #ffff66; }
.cyan { color: #66ccff; text-shadow: 0 0 15px #66ccff; }
.green { color: #00ff9d; text-shadow: 0 0 15px #00ff9d; }

.controls {
  display: flex;
  justify-content: center;
  gap: 30px;
  align-items: center;
  margin: 30px 0;
}

.bet-section {
  display: flex;
  align-items: center;
  gap: 15px;
}

.bet-input {
  padding: 12px;
  border-radius: 10px;
  border: 3px solid #ff00ff;
  width: 100px;
  background: #000;
  color: #ffff00;
  font-size: 18px;
  text-align: center;
}

.spin-button {
  padding: 15px 30px;
  background: linear-gradient(45deg, #ff00ff, #00ffff);
  border: 3px solid #ffff00;
  border-radius: 15px;
  color: #000;
  font-size: 24px;
  font-weight: bold;
  cursor: pointer;
  text-transform: uppercase;
  box-shadow: 0 0 20px #ffff00;
  transition: all 0.3s;
}

.spin-button:hover:not(:disabled) {
  transform: scale(1.1);
  box-shadow: 0 0 30px #ff00ff;
}

.spin-button:disabled {
  background: #666;
  border-color: #333;
  cursor: not-allowed;
  box-shadow: none;
}

.result {
  text-align: center;
  margin-top: 30px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  font-size: 24px;
  border: 3px solid #00ffff;
}

.win {
  color: #00ff9d;
  text-shadow: 0 0 15px #00ff9d;
}

.win-frame {
  border: 5px dashed #ffff00;
  border-radius: 10px;
  padding: 10px;
  animation: winBlink 0.5s infinite;
}

@keyframes winBlink {
  0% { border-color: #ffff00; box-shadow: 0 0 20px #ffff00; }
  50% { border-color: #ff00ff; box-shadow: 0 0 20px #ff00ff; }
  100% { border-color: #00ffff; box-shadow: 0 0 20px #00ffff; }
}

.lose {
  color: #ff3366;
  text-shadow: 0 0 15px #ff3366;
}

.ad-banner {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 200px;
  padding: 20px;
  background: linear-gradient(45deg, #ff00ff, #ffff00);
  color: #000;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  border-radius: 15px;
  border: 3px solid #00ffff;
  box-shadow: 0 0 25px #00ffff;
  text-transform: uppercase;
}

.left-ad {
  left: -250px;
}

.right-ad {
  right: -250px;
}